import React from 'react';
import PropTypes from 'prop-types';
import './chat.css';
import { Panel, PanelHeader, WriteBar, Banner, Avatar, WriteBarIcon, FixedLayout, Cell, PanelHeaderBack } from '@vkontakte/vkui';

const Chat = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home" style={{color: '#ad8ef5'}}/>}
		>
			–ß–∞—Ç
		</PanelHeader>
		<Banner
        before={
          <Avatar size={28} src="https://i.imgur.com/cNygXNz.png"/>
        }
        header="–í–∞–∂–Ω–æ!"
        subheader="–ó–∞–ø—Ä–µ—â–µ–Ω—ã —Å—Å—ã–ª–∫–∏, —Å–ø–∞–º, —Ñ–ª—É–¥. –ù–µ –æ—Å–∫–æ—Ä–±–ª—è–π—Ç–µ –¥—Ä—É–≥–∏—Ö –∏–≥—Ä–æ–∫–æ–≤ –∏ –±—É–¥—å—Ç–µ —É–≤–∞–∂–∏—Ç–µ–ª—å–Ω—ã."
      />
		 <div id="chatbox" style={{overflow: 'auto'}}>
		 {props.Messages}
		 </div>
		 <div style={{marginTop: 70}}>
		 </div>
		 <FixedLayout vertical="bottom">
		 <WriteBar
		        id="message"
            after={
							<React.Fragment>
              <WriteBarIcon mode="send" style={{color: '#ad8ef5'}} onClick={() => {
								if(document.getElementById('message').value.length > 1 && document.getElementById('message').value.length < 50){
								props.wsConnection.send(JSON.stringify({event: 'chat', name: props.Info.info.first_name, text: document.getElementById('message').value, photo: props.Info.info.photo_200}))
								document.getElementById('message').value = ""
							}
							}}/>
							</React.Fragment>
            }
            placeholder="–°–æ–æ–±—â–µ–Ω–∏–µ"
          />
			</FixedLayout>
	</Panel>
);

Chat.propTypes = {
	id: PropTypes.string.isRequired,
	Messages: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	wsConnection: PropTypes.func.isRequired,
	Info: PropTypes.func.isRequired,
};

export default Chat;
